import Bmob from "hydrogen-js-sdk";
let that;
export default {
	data() {
		return {
			operationId: '',
			detail: [],
			matterStatus:false,
		}
	},
	onLoad(options) {
		that = this;
		that.operationId = options.id
	},
	onShow() {
		that.getDetail(that.operationId)
	},

	methods: {
		//修改模式下设置缓存
		setStorage(){
			if(that.matterStatus == true){
				uni.showToast({
					title:"已经领过物料,不可修改！"
				})
			}else{
				uni.setStorageSync("products",that.detail)
				uni.navigateTo({
					url:'/pages/production/matterEnter/matterEnter?type=productionEdit&id='+that.operationId
				})
			}
		},
		
		getDetail(id) {
			const query = Bmob.Query('order_opreations');
			query.select("mattersId");
			query.get(id).then(res => {
				console.log('物料单详情', res)
				that.matterStatus = res.matterStatus
				that.detail = res.mattersId
			}).catch(err => {
				console.log(err)
			})
		},

	}
}
